import React, { useEffect, useMemo, useState, useCallback } from 'react'
import { useTable, useSortBy } from "react-table";
// Web3.
import { formatUnits, formatEther } from "@ethersproject/units";
// 
import Web3 from 'web3'
// Components.
import AaveRouteActions from "../poolData-helpers/AaveRouteActions"
import { useStaticPoolDataContext } from '~GRAPHQL/static_graph-pool-data-provider_formated';
import { useContentContextState } from '~/Code/Components/content_context';
//
import Icon from '../../base/icon/Icon'
import Loader from '~BASE/loader/loader'
import RedrawTimer from '~BASE/RedrawTimer/RedrawTimer'
// 
const Markett = () => {
//
  const { setIsMobileWidthVersion } = useStaticPoolDataContext()
  const { isMobile } = setIsMobileWidthVersion

  const { setPoolDataState, setUserAccountData,  setReserveTokensContext } = useContentContextState();
  const { assetData } = setPoolDataState
  const { reserveTokens } = setReserveTokensContext
  const { userAccountData } = setUserAccountData
//

  const [showActiveAssets, setShowActiveAssets] = useState(false);
  const [displayCurrency, setDisplayCurrency] = useState('native');
  

  let ethPrice = '4632.22'
  let convertNative = ['USD','ETH'].includes(displayCurrency)
  let showUsdPrice = (ethPrice && displayCurrency === 'USD')

 
  const columns = useMemo(
    () => [
      {
        Header: 'Token',
        accessor: value => <>
          <Icon showImg={true} showSpan={true} symbolChild={value.symbol} directory={'/assets-main'} />
        </> 
          //   sorter: (a, b) => convertValue(a.liquidityRate, a.decimals, convertNative?formatEther(a.price.priceInEth):1) - convertValue(b.availableLiquidity, b.decimals, convertNative?formatEther(b.price.priceInEth):1),

      },

      // {
      //   Header: 'Total borrowed' + (convertNative?` (${displayCurrency})` : ''),
      //   accessor: value => (every3NumberCommas(lazyround(formattedValue(value.totalCurrentVariableDebt, value.decimals, convertNative ? formatEther(value.price.priceInEth) : 1, showUsdPrice, ethPrice)))),
      //   sorter: (a, b) => convertValue(a.liquidityRate, a.decimals, convertNative?formatEther(a.price.priceInEth):1) - convertValue(b.availableLiquidity, b.decimals, convertNative?formatEther(b.price.priceInEth):1),
      //   sortDirections: ['descend', 'ascend'],
      // },
      {
        Header: 'Deposit APR',
        accessor: value => {
          let RAY = 10 ** 27
          let resultForAprSumm = value.liquidityRate.toString()
          let depositAPR = Number((+resultForAprSumm / RAY )) === 0 
            ? '0' 
            : Number(+resultForAprSumm / RAY * 100).toFixed(4) 
          return isMobile 
            ? <div style={{ width: '100%', justifyContent: 'space-between', display: 'flex' }}>
                <span>Deposit APR</span>
                <span>{depositAPR} %</span>
              </div>
            : depositAPR + ' %'
        },
        sorter: (a,b) => parseFloat(formatUnits(a.liquidityRate, 25)) - parseFloat(formatUnits(b.liquidityRate, 25)),
        sortDirections:['descend', 'ascend']
      },

      {
        Header: 'Stable APR',
        accessor: value => {
          let RAY = 10**27 // 10 to the power 27
          let stableBorrowAPR = Number((value.stableBorrowRate) / RAY) === 0
            ? '0'
            : Number((value.stableBorrowRate) / RAY * 100).toFixed(4)

          return isMobile 
            ? <div style={{ width: '100%', justifyContent: 'space-between', display: 'flex' }}>
              <span>Stable APR</span>
              <span>{stableBorrowAPR} %</span>
            </div>
            : stableBorrowAPR + ' %'
        },
        sorter: (a,b) => parseFloat(formatUnits(a.stableBorrowRate, 25)) - parseFloat(formatUnits(b.stableBorrowRate, 25)),
        sortDirections:['descend', 'ascend']
      },
      {
        Header: 'Variable APR',
        accessor: value => {
          let RAY = 10**27 // 10 to the power 27
          let variableBorrowAPR = Number((value.variableBorrowRate) / RAY) === 0
            ? '0'
            : Number((value.variableBorrowRate) / RAY * 100).toFixed(4)

          return isMobile
            ? <div style={{ width: '100%', justifyContent: 'space-between', display: 'flex' }}>
              <span>Variable APR</span>
              <span>{variableBorrowAPR} %</span>
            </div> 
            : variableBorrowAPR + ' %'
        },
        sorter: (a,b) => parseFloat(formatUnits(a.variableBorrowRate, 25)) - parseFloat(formatUnits(b.variableBorrowRate, 25)),
        sortDirections:['descend', 'ascend']
      },
      
      
      // {
      //   Header: 'Deposited' + (convertNative?` (${displayCurrency})`:''),
      //   accessor: value => (userAssetData[value.symbol] && userAssetData[value.symbol]['currentATokenBalance'])&&formattedValue(userAssetData[value.symbol]['currentATokenBalance'], value.decimals, convertNative?formatEther(value.price.priceInEth):1, showUsdPrice, ethPrice),
      // },
      // {
      //   Header: 'Stable Debt' + (convertNative?` (${displayCurrency})`:''),
      //   accessor: value => (userAssetData[value.symbol] && userAssetData[value.symbol]['currentATokenBalance'])&&formattedValue(userAssetData[value.symbol]['currentATokenBalance'], value.decimals, convertNative?formatEther(value.price.priceInEth):1, showUsdPrice, ethPrice),
      // },
      // {
      //   Header: 'Variable Debt' + (convertNative?` (${displayCurrency})`:''),
      //   accessor: value => (userAssetData[value.symbol])&&formattedValue(userAssetData[value.symbol]['currentVariableDebt'], value.decimals, convertNative?formatEther(value.price.priceInEth):1, showUsdPrice, ethPrice),
      // },
      
      {
        Header: 'Actions',
        accessor: value => {
          return ( <>
          <div className='doubleButtons'>
            {isMobile ? 'Actions' : ' '}
              {
               value.isActive && 
               <div className='flex-columnPremobile'>
                <AaveRouteActions 
                  type={"deposit"} 
                  classNameChild="zInd btn btn--red btn--small" 
                  assetData={value} 
                /> 
                <AaveRouteActions 
                  type={"reserve-overwiew"} 
                  classNameChild="reserve-button" 
                  assetData={value} 
                /> 
                <AaveRouteActions 
                  type={"borrow"} 
                  classNameChild="zInd btn btn--blue btn--small" 
                  assetData={value} 
                /> 
                </div>
                }
            </div>
          </> )
          
        }
      },

    ]
  )


  const columns2 = useMemo(
    () => [

      {
        Header: ' ',
        accessor: value => <div style={{position: 'absolute', right: '55%', top: '50%'}}> <Loader /> </div>,
      },
      
    ]
  )

  const Table = useCallback(({ columns, data, filterable }) => {
    // Use the state and functions returned from useTable to build your UI
    const {
      getTableProps,
      getTableBodyProps,
      headerGroups,
      rows,
      prepareRow,
      state,
      visibleColumns,
      preGlobalFilteredRows,
      setGlobalFilter,
    } = useTable({ columns, data },
      useSortBy       
    )
    

    const [isLoad, setLoaded] = useState()

      useEffect(() => {
          setLoaded(true)
      }, [])

    return (
      <div className="container">
        <div className={`${ isLoad ? 'active_block' : 'hidden_block' }`}>

        <RedrawTimer />
        <div className="dashboard__body flex-col">

    <div className='col--12 grow-1'>
      <div className='table table--sliding'>
          <table className='table' {...getTableProps()} >
            {!isMobile ?
              <thead>
                {headerGroups.map(headerGroup => (
                  <tr className='table-header__body'  {...headerGroup.getHeaderGroupProps()}>
                    {headerGroup.headers.map(column => (
                      <th
                        className='table-header__col'
                        style={{ justifyContent: 'flex-start' }}
                        { ...column.getHeaderProps(column.getSortByToggleProps()) }
                      >
                        {column.render('Header')}
                          <span>
                            {column.isSorted
                              ? column.isSortedDesc
                                ? '  ðŸ”½'
                                : '  ðŸ”¼'
                              : ''
                            }
                          </span>
                      </th>
                    ))}
                  </tr>
                ))}
              </thead>
                : ''
              }
                <tbody {...getTableBodyProps()}>
                  {rows.map(row => {
                    prepareRow(row)
                    return (
                      <tr  className='table-item table-value table-value--dark mb-8' {...row.getRowProps()}>
                        {row.cells.map(cell => {
                          return (
                            <td className='dataTables'
                              {...cell.getCellProps()}
                            >
                              {cell.render('Cell')}
                            </td>
                          )
                        })}
                      </tr>
                    )
                  })}
                </tbody>
          </table>
      </div>
    </div>

            </div>
          </div>
        </div>
    )
  }, []
)

  return (
      
    <>
        <Table
          data={
            assetData?.length === 0 && reserveTokens
              ? reserveTokens 
              : assetData
          }
          columns={assetData.length === 0 ? columns2 : columns} 
        />
    </>

  )

}


export default Markett